  name: 'PressActAwardDialog',
  options: {
    styleIsolation: 'shared',
  },
  components: {

    PressQRCodeWeb,
    PressBarcodeWeb,







    VanPopover: Popover,
  },
  props: {
    show: {
      type: Boolean,
      default: false,
      required: false,
    },
    issubscribe: {
      type: Boolean,
      default: false,
      required: false,
    },
    subBtnText: {
      type: String,
      default: '',
      required: false,
    },
    isShowMpButton: {
      type: Boolean,
      default: true,
      required: false,
    },
    propsData: {
      type: Object,
      default: () => ({
        brandName: '',
        brandicon:
          'https://igame-10037599.cos.ap-shanghai.myqcloud.com/380e4ce1-e731-c8be-eb5e-636f1763446b',
        shopName: '',
