            reportModule: 'gift_detail',
            giftBoxData: {
              expireTime: ' 2021.09.30',
            },
          },
        ],
        type: 'received',
        btnText: '',
      }),
    },
    water: {
      type: Object,
      default: () => ({
        goodscfg: {
          goodsname: '',
          goodsdetail:
            '&lt;p&gt;1&lt;br/&gt;2&lt;br/&gt;3&lt;br/&gt;4&lt;br/&gt;5&lt;br/&gt;69307.10-8.22&lt;/p&gt;&lt;p&gt;7&lt;/p&gt;&lt;p&gt;8&lt;br/&gt;9&lt;br/&gt;&lt;br/&gt;&lt;br/&gt;&lt;br/&gt;&lt;br/&gt;&lt;br/&gt;&lt;br/&gt;&lt;br/&gt;&lt;br/&gt;&lt;/p&gt;',
