      type: Array,
      default: () => [],
    },
    useTipClass: {
      type: Boolean,
      default: true,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    'clickBtn',
    'clickButton',
    'clickCloseButton',
    'clickEnter',
    'clickExtraAct',
    'clickGoodItem',
    'clickModfy',
    'clickSub',
    'clickUsedShop',
    'gotoGoodsDetailDesc',
    'update:show',
  ],
  data() {
    return {
      showPopoverIndex: -1,
    };
  },
  computed: {
    extraAct() {
      return this.propsData.extraAct || this.water.extraAct || null;
    },
  },
  watch: {},
  methods: {
    clickButton(event) {
      this.$emit('clickButton', event);
    },
    closeDialog() {
      this.$emit('update:show', false);
      this.$emit('clickCloseButton');
    },
    onClickEnterButton() {
      this.$emit('clickEnter');
    },
    onClickSubButton() {
      this.$emit('clickSub');
    },
    previewImage(url) {
      if (!url) {
        return;
      }
      imagePreview({
        images: [url],
        closeable: true,
        showIndex: false,
        zIndex: 100,
      });
    },
    showGiftTip(index) {
      this.showPopoverIndex = index;
      setTimeout(() => {
        this.showPopoverIndex = -1;
      }, 2000);
    },
    onClickModfy() {
      this.$emit('clickModfy');
    },
    // onClickBtn() {
    //   this.$emit('clickBtn');
    // },
    onClickUsedShop() {
      this.$emit('clickUsedShop');
    },
    doNothing() {},
    onClickExtraActBtn(url) {
      this.$emit('clickExtraAct', url);
    },
    onGoodItemClick(waterItem, index) {
      this.$emit('clickGoodItem', waterItem, index);
    },
    gotoGoodsDetailDesc(waterItem, index) {
      this.$emit('gotoGoodsDetailDesc', waterItem, index);
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
